function UfoColumn({ UfoOne, UfoTwo, UfoThree, className }) {
  return (
    <div className={`self-center w-full ${className}`}>
      <img
        src={`${UfoOne === "yellow" ? UfoYellow : UfoBlue}`}
        alt=" "
        className="w-1/3 m-auto mb-6"
      />
      <img
        src={`${UfoTwo === "yellow" ? UfoYellow : UfoBlue}`}
        alt=" "
        className="w-1/3 m-auto mb-6"
      />
      <img
        src={`${UfoThree === "yellow" ? UfoYellow : UfoBlue}`}
        alt=" "
        className="w-1/3 m-auto "
      />
    </div>
  );
}

export default UfoColumn;
