import React, { useState, useEffect } from "react";

function Cross({ parent, className }) {
  const [width, setWidth] = useState(1);
  const [height, setHeight] = useState(1);

  useEffect(() => {
    function getDimentions(parent) {
      setWidth(parent.current.clientWidth);
      setHeight(parent.current.clientHeight);
    }
    setTimeout(() => getDimentions(parent), 400);
    let timeoutId = null;
    const resizeListener = () => {
      clearTimeout(timeoutId);
      timeoutId = setTimeout(() => getDimentions(parent), 100);
    };
    window.addEventListener("resize", resizeListener);
    return () => {
      window.removeEventListener("resize", resizeListener);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  return (
    <svg
      viewBox={`0 0 ${width} ${height}`}
      stroke="#011759"
      strokeWidth="4"
      xmlns="http://www.w3.org/2000/svg"
      xmlnsXlink="http://www.w3.org/1999/xlink"
      className={className}
    >
      <polyline points={`0 0 ${width} ${height}`} />
      <polyline points={`${width} 0 0 ${height}`} />
    </svg>
  );
}

export default Cross;
