import React from "react";
import { graphql, useStaticQuery } from "gatsby";
import Img from "gatsby-image";
import UfoColumn from "../components/UfoColumn.js";

function FourthScreen() {
  const images = useStaticQuery(graphql`
    fragment regularImage on File {
      sharp: childImageSharp {
        fluid {
          ...GatsbyImageSharpFluid
        }
      }
    }
    query {
      Collage: file(relativePath: { eq: "Collage.png" }) {
        ...regularImage
      }
    }
  `);
  return (
    <section className=" flex w-full">
      {/* Left column */}
      <div className="relative w-full flex-1">
        <div className="absolute h-full w-full border-theme  border-r-4  flex flex-col-reverse ">
          <div className="h-40"></div>
          <UfoColumn UfoTwo="yellow" className="hidden lg:block" />
        </div>
        <div className="border-l-4"></div>
      </div>
      {/* Left column */}
      {/* Center column */}
      <div className="flex-0  max-w-80 custom-width">
        <div className="relative flex ">
          <Img
            fluid={images.Collage.sharp.fluid}
            alt=""
            className="w-full"
          />
        </div>
        <button className="bg-black text-5xl font-bold text-theme-yellow m-auto block my-20 px-6 text-shadow-pink">
          
        </button>
      </div>
      {/* Center column */}
      {/* Right column */}
      {/* <div className="relative w-full flex-1">
        <div className="absolute h-full w-full border-theme  border-l-4 flex flex-col">
          <div className="h-40"></div>
          <UfoColumn
            UfoOne="yellow"
            UfoThree="yellow"
            className="hidden lg:block"
          />
        </div>
      </div> */}
      <div className="relative w-full flex-1">
        <div className="absolute h-full w-full border-theme border-l-4 flex flex-col">
          <div className="h-40"></div>
          <UfoColumn UfoTwo="yellow" className="hidden lg:block" />
        </div>
        <div className="border-r-4"></div>
      </div>
      {/* <div className="relative w-full flex flex-col flex-1 justify-end">
        <div className="absolute h-full w-full border-theme  border-l-4"></div>
      </div> */}
      {/* Right column */}
    </section>
  );
}

export default FourthScreen;
