import React, { useState, useEffect } from "react";

function MatchHeightBox({ parent, className, children }) {
  const [height, setHeight] = useState(1);

  useEffect(() => {
    setTimeout(() => setHeight(parent.current.offsetHeight), 300);
    let timeoutId = null;
    const resizeListener = () => {
      clearTimeout(timeoutId);
      timeoutId = setTimeout(() => setHeight(parent.current.offsetHeight), 50);
    };
    window.addEventListener("resize", resizeListener);
    return () => {
      window.removeEventListener("resize", resizeListener);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  return (
    <div className={className} style={{ height: height }}>
      {children}
    </div>
  );
}

export default MatchHeightBox;
