import React, { useRef } from "react";
import { graphql, useStaticQuery } from "gatsby";
import Img from "gatsby-image";
import UfoBlue from "../images/UfoBlue.png";
import UfoYellow from "../images/UfoYellow.png";
import Fasade1 from "../images/Fasade1.jpg";
import Fasade2 from "../images/Fasade2.jpg";
import Cross from "../components/Cross.js";
import UfoColumn from "../components/UfoColumn.js";

function ThirdScreen() {
  const images = useStaticQuery(graphql`
    fragment regularImage on File {
      sharp: childImageSharp {
        fluid {
          ...GatsbyImageSharpFluid
        }
      }
    }
    query {
      Loft1: file(relativePath: { eq: "Loft1.jpg" }) {
        ...regularImage
      }
      Loft2: file(relativePath: { eq: "Loft2.jpg" }) {
        ...regularImage
      }
    }
  `);
  const crossContainer = useRef(null);
  return (
    <section className=" flex w-full">
      {/* Left column */}
      <div className="relative w-full flex flex-col flex-1 justify-between">
        <div className="">
          <div className=" border-theme border-b-4 hidden lg:block">
            <p className="font-medium text-3xl text-right p-4 "></p>
          </div>
        </div>
        <div className="absolute h-full w-full border-theme  border-r-4"></div>
        <UfoColumn
          UfoOne="yellow"
          UfoTwo="yellow"
          className="hidden lg:block"
        />
        <div
          className="h-32 border-theme border-b-4 border-r-4 border-t-4"
          style={{ backgroundImage: `url(${Fasade1})` }}
        ></div>
      </div>
      {/* Left column */}
      {/* Center column */}
      <div className="flex-0 max-w-80 custom-width">
        <div className="flex flex-col-reverse md:flex-row">
          <div className="flex-1">
            <Img
              fluid={images.Loft1.sharp.fluid}
              alt=""
              className="w-full flex-1"
            />
          </div>
          <div className="flex-1 border-theme md:border-l-4">
            <div className="p-4 flex flex-col justify-between h-full">
              <img src={UfoBlue} alt=" " className="max-w-20" />
              <h2 className="text-center font-bold text-4xl text-theme-yellow text-shadow-pink">
                 
              </h2>
              <img
                src={UfoBlue}
                alt=" "
                className="max-w-20 block ml-auto"
              />
            </div>
          </div>
        </div>
        <div
          className="h-32 border-theme border-t-4 border-b-4 relative"
          ref={crossContainer}
        >
          <Cross parent={crossContainer} />
        </div>
        <Img
          fluid={images.Loft2.sharp.fluid}
          alt=""
          className="w-full h-auto"
        />
        <div className=" flex items-center border-theme border-t-4 text-lg font-bold">
          <p className="p-4">
                    .  
            ,       ,   :&#41;
                   .
          </p>
        </div>
        <div className="h-32 border-theme border-t-4 border-b-4 relative flex items-center">
          <div className="w-full h-20 flex justify-around items-center absolute">
            <img
              src={UfoBlue}
              alt=" "
              className=" md:h-inherit w-1/5 md:w-auto px-2 sm:px-6"
            />
            <img
              src={UfoYellow}
              alt=" "
              className="md:h-inherit w-1/5 md:w-auto px-2 sm:px-6"
            />
            <img
              src={UfoBlue}
              alt=" "
              className="md:h-inherit w-1/5 md:w-auto px-2 sm:px-6"
            />
            <img
              src={UfoYellow}
              alt=" "
              className="md:h-inherit w-1/5 md:w-auto px-2 sm:px-6"
            />
            <img
              src={UfoBlue}
              alt=" "
              className="md:h-inherit w-1/5 md:w-auto px-2 sm:px-6"
            />
          </div>
        </div>
      </div>
      {/* Center column */}
      {/* Right column */}
      {/* <div className="relative w-full flex flex-col flex-1 justify-end">
        <div className="absolute h-full w-full border-theme  border-l-4"></div>
        <div
          className="h-32 border-theme border-b-4 border-t-4"
          style={{ backgroundImage: `url(${Fasade2})` }}
        ></div>
      </div> */}
      <div className="relative w-full flex flex-col flex-1 justify-end">
        <div className="absolute h-full w-full border-theme  border-l-4"></div>
        <div
          className="h-32 border-theme border-b-4 border-r-4 border-t-4"
          style={{ backgroundImage: `url(${Fasade2})` }}
        ></div>
      </div>
      {/* Right column */}
    </section>
  );
}

export default ThirdScreen;
