import React from "react";
import { graphql, useStaticQuery } from "gatsby";
import Img from "gatsby-image";
import UfoBlue from "../images/UfoBlue.png";
import House from "../images/House.jpg";

function SecondScreen() {
  const images = useStaticQuery(graphql`
    fragment regularImage on File {
      sharp: childImageSharp {
        fluid {
          ...GatsbyImageSharpFluid
        }
      }
    }
    query {
      Ufo: file(relativePath: { eq: "parallax/UFO.png" }) {
        ...regularImage
      }
      CableCar: file(relativePath: { eq: "CableCar.jpg" }) {
        ...regularImage
      }
    }
  `);
  return (
    <section className=" flex w-full">
      {/* Left column */}
      <div className="relative w-full flex flex-col flex-1 justify-between">
        <div className="">
          <div className="h-32 border-theme border-b-4 "></div>
          <div className=" border-theme border-b-4 hidden lg:block">
            <p className="font-medium text-3xl text-right p-4 "></p>
          </div>
        </div>
        <div className="absolute h-full w-full border-theme  border-r-4"></div>
        <div className="h-32 border-theme border-b-4 border-r-4 border-t-4"></div>
      </div>
      {/* Left column */}
      {/* Center column */}
      <div className="flex-0 max-w-80 custom-width">
        <div className="h-32 flex justify-between py-4 px-4 border-theme border-b-4  items-center">
          <img
            src={UfoBlue}
            alt=" "
            className="h-full hidden md:block"
          />
          <h1 className="text-theme-yellow text-5xl font-bold text-shadow-pink text-center w-full">
            15  2020
          </h1>
          <img
            src={UfoBlue}
            alt=" "
            className="h-full hidden md:block"
          />
        </div>
        <div className="flex flex-col md:flex-row">
          <div className="flex-1 border-theme md:border-r-4">
            <p className="text-lg p-4 font-bold">
              <span className="text-white">I Want To Be Rave </span> 
                 .   : ,
              ,   .      .
                 ,        .
                      ,  
              .
            </p>
          </div>
          <div className="flex-1">
            <Img
              fluid={images.CableCar.sharp.fluid}
              alt=""
              className="w-full flex-1"
            />
          </div>
        </div>
        <div
          className="w-full h-32 border-theme border-t-4 border-b-4 bg-size-full "
          style={{ backgroundImage: `url(${House})` }}
        ></div>
      </div>
      {/* Center column */}
      {/* Right column */}
      <div className="relative w-full flex flex-col flex-1 justify-between">
        <div className="h-32 border-theme border-b-4 border-l-4"></div>
        <div className="absolute h-full w-full border-theme  border-l-4"></div>
        <div className="h-32 border-theme border-b-4 border-t-4"></div>
      </div>
      {/* Right column */}
    </section>
  );
}

export default SecondScreen;
