import React, { useRef } from "react";
import { graphql, useStaticQuery } from "gatsby";
import Img from "gatsby-image";
import Park from "../images/Park.jpg";
import MatchHeightBox from "./MatchHeightBox.js";
import Cross from "../components/Cross.js";

function FourthScreen() {
  const images = useStaticQuery(graphql`
    fragment regularImage on File {
      sharp: childImageSharp {
        fluid {
          ...GatsbyImageSharpFluid
        }
      }
    }
    query {
      Palms: file(relativePath: { eq: "Palms.jpg" }) {
        ...regularImage
      }
    }
  `);
  const textContainer = useRef(null);
  const box = useRef(null);
  const bigBox = useRef(null);

  return (
    <section className=" flex w-full">
      {/* Left column */}
      <div className="relative w-full flex flex-col flex-1">
        <div className="h-24 border-theme border-b-4 hidden lg:block flex-none">
          <p className="font-medium text-3xl text-right p-4 "></p>
        </div>
        <div
          className=" border-theme border-r-4 flex-grow hidden lg:block"
          ref={bigBox}
        >
          <Cross parent={bigBox} className="flex w-full" />
        </div>
        <div className="absolute h-full w-full border-theme  border-r-4 border-b-4 top-0"></div>
      </div>
      {/* Left column */}
      {/* Center column */}
      <div className="flex-0 max-w-80 custom-width">
        <div className="w-full border-theme border-b-4">
          <div className="border-theme border-b-4" ref={textContainer}>
            <p className="w-full px-8 py-8 text-xl font-bold text-shadow-pink ">
              <span className="text-white"> :</span>  
              ,  .      
              ,     .   , 
              , <span className="text-theme-yellow">free bar</span>  
               . ,     .
            </p>
          </div>
          <Img
            fluid={images.Palms.sharp.fluid}
            alt=""
            className="w-full h-auto flex-1"
          />
        </div>
      </div>
      {/* Center column */}
      {/* Right column */}
      <div className="relative w-full border-b-4 border-theme flex flex-col flex-1">
        <MatchHeightBox
          parent={textContainer}
          className=" border-l-4 border-theme relative flex-none hidden lg:block"
        >
          <div
            className="w-full h-full absolute top-0 border-theme border-b-4"
            ref={box}
          ></div>
          <Cross parent={box} />
        </MatchHeightBox>
        <div
          className="w-full h-full bg-cover bg-right flex-shrink hidden lg:block"
          style={{ backgroundImage: `url(${Park})` }}
        />
        <div className="absolute h-full w-full border-theme  border-l-4"></div>
        <MatchHeightBox
          parent={textContainer}
          className="border-t-4 border-l-4  border-theme flex-none hidden lg:block"
        >
          <Cross parent={box} />
        </MatchHeightBox>
      </div>
      {/* Right column */}
    </section>
  );
}

export default FourthScreen;
