import React, { useEffect, useState, useRef } from "react";
import { graphql, useStaticQuery } from "gatsby";
import Img from "gatsby-image";
import useIntersect from "./utils/useIntersect";

function ParallaxHero() {
  const images = useStaticQuery(graphql`
    fragment regularImage on File {
      sharp: childImageSharp {
        fluid {
          ...GatsbyImageSharpFluid
        }
      }
    }
    query {
      Ufo: file(relativePath: { eq: "parallax/UFO.png" }) {
        ...regularImage
      }
      BG_Up: file(relativePath: { eq: "parallax/BG_up.png" }) {
        ...regularImage
      }
      BG_Down: file(relativePath: { eq: "parallax/BG_down.png" }) {
        ...regularImage
      }
      Bushes: file(relativePath: { eq: "parallax/Kusti.png" }) {
        ...regularImage
      }
      Branches: file(relativePath: { eq: "parallax/Vetki.png" }) {
        ...regularImage
      }
      TextOne: file(relativePath: { eq: "parallax/TextOne.png" }) {
        ...regularImage
      }
      TextTwo: file(relativePath: { eq: "parallax/TextTwo.png" }) {
        ...regularImage
      }
      TextThree: file(relativePath: { eq: "parallax/TextThree.png" }) {
        ...regularImage
      }
    }
  `);

  const [ref, entry] = useIntersect({
    threshold: 0,
  });
  const isInView = useRef(null);
  //   const [isInView, setIsInView] = useState(null);
  const [firstLayerX, setfirstLayerX] = useState(0);
  const [firstLayerY, setfirstLayerY] = useState(0);
  const [SecondLayerX, setSecondLayerX] = useState(0);
  const [SecondLayerY, setSecondLayerY] = useState(0);
  const [ThirdLayerX, setThirdLayerX] = useState(0);
  const [ThirdLayerY, setThirdLayerY] = useState(0);
  const [TextLayerX, setTextLayerX] = useState(0);
  const [TextLayerY, setTextLayerY] = useState(0);
  useEffect(() => {
    isInView.current = entry.isIntersecting;
    function calcParallax(e) {
      let xPos = e.clientX / window.innerWidth;
      let yPos = e.clientY / window.innerHeight;
      setfirstLayerX(4 - 8 * xPos + "%");
      setfirstLayerY(4 - 8 * yPos + "%");
      setSecondLayerX(2 - 4 * xPos + "%");
      setSecondLayerY(2 - 4 * yPos + "%");
      setThirdLayerX(6 - 12 * xPos + "%");
      setThirdLayerY(6 - 12 * yPos + "%");
      setTextLayerX(2 - 4 * xPos + "px");
      setTextLayerY(2 - 4 * yPos + "px");
    }
    let timeoutId = null;
    function mouseListener(e) {
      if (isInView.current) {
        clearTimeout(timeoutId);
        timeoutId = setTimeout(() => calcParallax(e), 5);
      }
    }
    document.addEventListener("mousemove", mouseListener);
    return () => {
      document.removeEventListener("mousemove", mouseListener);
    };
  }, [entry.isIntersecting]);
  return (
    <>
      <svg
        xmlns="http://www.w3.org/2000/svg"
        xmlnsXlink="http://www.w3.org/1999/xlink"
        viewBox="0 0 1170 1080"
      />
      <div className="absolute top-0 w-full h-full" ref={ref}>
        <Img
          fluid={images.BG_Down.sharp.fluid}
          alt=""
          className="w-full h-auto top-0 scale-115"
          style={{
            marginLeft: firstLayerX,
            marginTop: firstLayerY,
            position: "absolute",
          }}
        />
        <Img
          fluid={images.BG_Up.sharp.fluid}
          alt=""
          className="w-full h-auto  top-0 scale-115"
          style={{
            marginLeft: ThirdLayerX,
            marginTop: ThirdLayerY,
            position: "absolute",
          }}
        />
        <Img
          fluid={images.Branches.sharp.fluid}
          alt=""
          className="w-full h-auto  top-0 scale-115"
          style={{
            marginLeft: SecondLayerX,
            marginTop: SecondLayerY,
            position: "absolute",
          }}
        />
        <Img
          fluid={images.Bushes.sharp.fluid}
          alt=""
          className="w-full h-auto  top-0 scale-115"
          style={{
            marginLeft: SecondLayerX,
            marginTop: SecondLayerY,
            position: "absolute",
          }}
        />
        <Img
          fluid={images.Ufo.sharp.fluid}
          alt=""
          className="w-full h-auto  top-0"
          style={{ position: "absolute" }}
        />
        <Img
          fluid={images.TextThree.sharp.fluid}
          alt=""
          className="w-full h-auto  top-0 "
          style={{
            marginLeft: TextLayerX,
            marginTop: TextLayerY,
            position: "absolute",
          }}
        />
        <Img
          fluid={images.TextOne.sharp.fluid}
          alt=""
          className="w-full h-auto  top-0 "
          style={{
            marginLeft: TextLayerX,
            marginTop: TextLayerY,
            position: "absolute",
          }}
        />

        <Img
          fluid={images.TextTwo.sharp.fluid}
          alt=""
          className="w-full h-auto top-0 "
          style={{ position: "absolute" }}
        />
      </div>
    </>
  );
}

export default ParallaxHero;
